            <div class="vis-content">
                <div id="map"></div>
                <div id='state-legend' class='legend'>
                    <div>
                        <span style='background-color: #E11519'></span>Incultivable&nbsp;
                        <span style='background-color: #FEB667'></span>Casi incultivable&nbsp;
                        <span style='background-color: #FFFFC7'></span>Apenas adecuado&nbsp;
                        <span style='background-color: #B2E6AB'></span>Sin cambios&nbsp;
