<script>

    import * as mapboxgl from 'mapbox-gl';

    export default {
        name: 'app',
        components: {},
        data: () => {
            return {}
        },

        mounted: function () {
            this.loadData();
            this.drawVis();
        },

        methods: {
            loadData: function () {

            },

            drawVis: function () {
                mapboxgl.accessToken = "pk.eyJ1Ijoib3NjYXJhbCIsImEiOiJjanBsa3d4YnUwZmJiNDJubG14bmdtemZvIn0.MEhhIa67a_7R8FHDAEoPFg";

                let map = new mapboxgl.Map({
                    container: 'map',
                    // style: 'mapbox://styles/oscaral/cjyanzgzj0frp1cl09v7567l2',
                    style: 'mapbox://styles/oscaral/cjyetd20e2co91cky1irkojuf',
                    center: [-69.411, 0.844],
                    zoom: 2.5
                });

                let attributtionControl = new mapboxgl.AttributionControl({
                    compact: true,
                    customAttribution: "International Center for Tropical Agriculture"
                });

                map.addControl(attributtionControl);
                map.addControl(new mapboxgl.NavigationControl(), 'top-right');
                map.addControl(new mapboxgl.FullscreenControl());

                map.on('load', function() {

                    var layers = map.getStyle().layers;
                    var firstSymbolId;
                    for (var i = 0; i < layers.length; i++) {
                        if (layers[i].type === 'symbol') {
                            firstSymbolId = layers[i].id;
                            break;
                        }
                    }

                    console.log("Añadiendo raster");

                    map.addLayer({
                        id: 'oscaral.dn07zd8i',
                        type: 'raster',
                        source: {
                            type: 'raster',
                            tiles: ['https://api.mapbox.com/v4/oscaral.dn07zd8i/{z}/{x}/{y}.png?access_token=pk.eyJ1Ijoib3NjYXJhbCIsImEiOiJjanBsa3d4YnUwZmJiNDJubG14bmdtemZvIn0.MEhhIa67a_7R8FHDAEoPFg'],
                        },
                        minzoom: 0,
                        maxzoom: 22
                    }, firstSymbolId);

                });

                map.resize();


            }
        }
    }
</script>

